export default function HighlightTextCtrl($scope, $element) {
  const $ctrl = this;
  const regexFlags = 'gi';
  const template = '<span class="highlight">$1</span>';
  let regex = null;

  const render = (state, prevState) => {
    if (regex === null || state.term !== prevState.term) {
      regex = new RegExp(`(${state.term})`, regexFlags);
    }

    if (state.term) {
      $element.empty();
      $element.html(state.content.replace(regex, template));
    } else {
      $element.text(state.content);
    }
  };

  const init = (termExpr, contentExpr) => {
    const unregisterWatch = $scope.$watch((scope) => ({
      term: termExpr(scope),
      content: contentExpr(scope),
    }), render, true);

    $element.on('$destroy', unregisterWatch);
  };

  Object.assign($ctrl, {
    init,
  });
}



